// 
export interface User {
  id: string;
  email: string;
  displayName?: string;
}

class EmailAuthService {
  private user: User | null = null;
  private authStateListeners: ((user: User | null) => void)[] = [];

  constructor() {
    // 
    this.loadUserFromStorage();
  }

  // 
  private loadUserFromStorage(): void {
    const stored = localStorage.getItem('memo-app-user');
    if (stored) {
      try {
        this.user = JSON.parse(stored);
      } catch {
        localStorage.removeItem('memo-app-user');
      }
    }
  }

  // 
  private saveUserToStorage(user: User | null): void {
    if (user) {
      localStorage.setItem('memo-app-user', JSON.stringify(user));
    } else {
      localStorage.removeItem('memo-app-user');
    }
  }

  // 
  onAuthStateChanged(callback: (user: User | null) => void): () => void {
    this.authStateListeners.push(callback);
    
    // 
    callback(this.user);
    
    // 
    return () => {
      const index = this.authStateListeners.indexOf(callback);
      if (index > -1) {
        this.authStateListeners.splice(index, 1);
      }
    };
  }

  // 
  private notifyAuthStateChange(): void {
    this.authStateListeners.forEach(callback => callback(this.user));
  }

  // 
  private isValidEmail(email: string): boolean {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  }

  // 
  private isValidPassword(password: string): boolean {
    return password.length >= 6;
  }

  // 
  private getErrorMessage(error: any): string {
    const errorMessages: { [key: string]: string } = {
      'auth/email-already-in-use': '',
      'auth/invalid-email': '',
      'auth/operation-not-allowed': '',
      'auth/weak-password': '6',
      'auth/user-disabled': '',
      'auth/user-not-found': '',
      'auth/wrong-password': '',
      'auth/too-many-requests': '',
      'auth/network-request-failed': '',
      'auth/invalid-credential': '',
    };

    return errorMessages[error.code] || '';
  }

  // 
  async createUserWithEmailAndPassword(email: string, password: string): Promise<User> {
    // 
    if (!this.isValidEmail(email)) {
      throw new Error('');
    }
    if (!this.isValidPassword(password)) {
      throw new Error('6');
    }

    try {
      // 
      // API
      const user: User = {
        id: Date.now().toString(),
        email: email,
        displayName: email.split('@')[0],
      };

      this.user = user;
      this.saveUserToStorage(user);
      this.notifyAuthStateChange();

      return user;
    } catch (error) {
      throw new Error(this.getErrorMessage(error));
    }
  }

  // 
  async signInWithEmailAndPassword(email: string, password: string): Promise<User> {
    // 
    if (!this.isValidEmail(email)) {
      throw new Error('');
    }
    if (!password) {
      throw new Error('');
    }

    try {
      // 
      // API
      const user: User = {
        id: Date.now().toString(),
        email: email,
        displayName: email.split('@')[0],
      };

      this.user = user;
      this.saveUserToStorage(user);
      this.notifyAuthStateChange();

      return user;
    } catch (error) {
      throw new Error(this.getErrorMessage(error));
    }
  }

  // 
  async signOut(): Promise<void> {
    this.user = null;
    this.saveUserToStorage(null);
    this.notifyAuthStateChange();
  }

  // 
  getCurrentUser(): User | null {
    return this.user;
  }

  // 
  isAuthenticated(): boolean {
    return this.user !== null;
  }
}

export const emailAuthService = new EmailAuthService();
