import React, { useState, useRef, useEffect } from 'react';
import { 
  Mic, 
  MicOff, 
  Volume2, 
  VolumeX, 
  // Play,
  // Pause,
  Square,
  AlertCircle,
  CheckCircle,
  Clock
} from 'lucide-react';
import { speechService, speechSynthesisService, SpeechRecognitionResult } from '../services/speechService';
import { CreateMemoData } from '../types';

interface VoiceInputProps {
  onMemoCreate: (memo: CreateMemoData) => void;
  onClose: () => void;
}

interface VoiceSession {
  id: string;
  transcript: string;
  timestamp: Date;
  confidence: number;
  isFinal: boolean;
}

export const VoiceInput: React.FC<VoiceInputProps> = ({ onMemoCreate, onClose }) => {
  const [isListening, setIsListening] = useState(false);
  const [isSpeaking, setIsSpeaking] = useState(false);
  const [currentTranscript, setCurrentTranscript] = useState('');
  const [voiceSessions, setVoiceSessions] = useState<VoiceSession[]>([]);
  const [error, setError] = useState<string | null>(null);
  const [processingTranscript, setProcessingTranscript] = useState(false);
  const [selectedLanguage, setSelectedLanguage] = useState('ja-JP');
  
  const sessionIdRef = useRef<string>('');
  const finalTranscriptRef = useRef<string>('');

  useEffect(() => {
    if (!speechService.isAvailable()) {
      setError('ChromeEdgeSafari');
    }
  }, []);

  const startListening = () => {
    setError(null);
    setCurrentTranscript('');
    finalTranscriptRef.current = '';
    sessionIdRef.current = Date.now().toString();
    setIsListening(true);

    speechService.startListening(
      (result: SpeechRecognitionResult) => {
        setCurrentTranscript(result.transcript);
        
        if (result.isFinal) {
          finalTranscriptRef.current = result.transcript;
          setVoiceSessions(prev => [...prev, {
            id: sessionIdRef.current,
            transcript: result.transcript,
            timestamp: new Date(),
            confidence: result.confidence,
            isFinal: true
          }]);
        }
      },
      (error: string) => {
        setError(`: ${error}`);
        setIsListening(false);
      },
      () => {
        setIsListening(false);
      },
      {
        language: selectedLanguage,
        continuous: true,
        interimResults: true
      }
    );
  };

  const stopListening = () => {
    speechService.stopListening();
    setIsListening(false);
  };

  const processTranscript = async (transcript: string) => {
    setProcessingTranscript(true);
    
    try {
      // 
      const analysis = analyzeTranscript(transcript);
      
      const memoData: CreateMemoData = {
        content: analysis.content,
        category: analysis.category,
        is_task: analysis.is_task,
        priority: analysis.priority,
        tags: analysis.tags
      };

      onMemoCreate(memoData);
      setVoiceSessions([]);
      setCurrentTranscript('');
      
    } catch (error) {
      setError('');
    } finally {
      setProcessingTranscript(false);
    }
  };

  const analyzeTranscript = (text: string) => {
    // 
    const lowerText = text.toLowerCase();
    
    // 
    const taskKeywords = ['', '', '', '', '', '', 'todo', ''];
    const isTask = taskKeywords.some(keyword => lowerText.includes(keyword));
    
    // 
    let priority = 1;
    if (lowerText.includes('') || lowerText.includes('') || lowerText.includes('')) {
      priority = 3;
    } else if (lowerText.includes('') || lowerText.includes('')) {
      priority = 2;
    }
    
    // 
    let category = '';
    if (lowerText.includes('') || lowerText.includes('') || lowerText.includes('') || lowerText.includes('')) {
      category = '';
    } else if (lowerText.includes('') || lowerText.includes('') || lowerText.includes('')) {
      category = '';
    } else if (lowerText.includes('') || lowerText.includes('') || lowerText.includes('')) {
      category = '';
    } else if (lowerText.includes('') || lowerText.includes('') || lowerText.includes('')) {
      category = '';
    } else if (lowerText.includes('') || lowerText.includes('') || lowerText.includes('')) {
      category = '';
    }
    
    // 
    const tags: string[] = [];
    if (lowerText.includes('')) tags.push('');
    if (lowerText.includes('')) tags.push('');
    if (lowerText.includes('')) tags.push('');
    if (lowerText.includes('')) tags.push('');
    if (lowerText.includes('')) tags.push('');
    if (lowerText.includes('')) tags.push('');
    
    return {
      content: text,
      category,
      is_task: isTask,
      priority,
      tags
    };
  };

  const speakText = async (text: string) => {
    try {
      setIsSpeaking(true);
      await speechSynthesisService.speak(text, {
        lang: 'ja-JP',
        rate: 0.9,
        pitch: 1.0
      });
    } catch (error) {
      setError('');
    } finally {
      setIsSpeaking(false);
    }
  };

  const stopSpeaking = () => {
    speechSynthesisService.stop();
    setIsSpeaking(false);
  };

  const clearSessions = () => {
    setVoiceSessions([]);
    setCurrentTranscript('');
    setError(null);
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg shadow-xl w-full max-w-2xl max-h-[90vh] overflow-y-auto">
        <div className="p-6">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-xl font-bold"></h2>
            <button
              onClick={onClose}
              className="p-2 text-gray-400 hover:text-gray-600 transition-colors"
            >
              
            </button>
          </div>

          {error && (
            <div className="mb-4 p-3 bg-red-100 text-red-800 rounded-lg flex items-center gap-2">
              <AlertCircle size={16} />
              {error}
            </div>
          )}

          {/*  */}
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              
            </label>
            <select
              value={selectedLanguage}
              onChange={(e) => setSelectedLanguage(e.target.value)}
              className="input"
              disabled={isListening}
            >
              <option value="ja-JP"></option>
              <option value="en-US">English (US)</option>
              <option value="en-GB">English (UK)</option>
              <option value="ko-KR"></option>
              <option value="zh-CN"> ()</option>
            </select>
          </div>

          {/*  */}
          <div className="mb-6">
            <div className="flex items-center gap-4 mb-4">
              {!isListening ? (
                <button
                  onClick={startListening}
                  disabled={!speechService.isAvailable()}
                  className="btn btn-primary flex items-center gap-2"
                >
                  <Mic size={20} />
                  
                </button>
              ) : (
                <button
                  onClick={stopListening}
                  className="btn btn-danger flex items-center gap-2"
                >
                  <MicOff size={20} />
                  
                </button>
              )}

              {currentTranscript && (
                <button
                  onClick={() => speakText(currentTranscript)}
                  disabled={isSpeaking}
                  className="btn btn-secondary flex items-center gap-2"
                >
                  {isSpeaking ? <VolumeX size={20} /> : <Volume2 size={20} />}
                  {isSpeaking ? '...' : ''}
                </button>
              )}

              {isSpeaking && (
                <button
                  onClick={stopSpeaking}
                  className="btn btn-danger flex items-center gap-2"
                >
                  <Square size={20} />
                  
                </button>
              )}
            </div>

            {isListening && (
              <div className="flex items-center gap-2 text-blue-600 mb-2">
                <Clock size={16} className="animate-pulse" />
                <span className="text-sm">...</span>
              </div>
            )}
          </div>

          {/*  */}
          {currentTranscript && (
            <div className="mb-4">
              <h3 className="text-sm font-medium text-gray-700 mb-2"></h3>
              <div className="p-3 bg-gray-100 rounded-lg">
                <p className="text-gray-800">{currentTranscript}</p>
              </div>
            </div>
          )}

          {/*  */}
          {voiceSessions.length > 0 && (
            <div className="mb-6">
              <h3 className="text-sm font-medium text-gray-700 mb-2"></h3>
              <div className="space-y-2 max-h-40 overflow-y-auto">
                {voiceSessions.map((session) => (
                  <div key={session.id} className="p-3 bg-blue-50 rounded-lg">
                    <div className="flex items-center justify-between mb-2">
                      <span className="text-xs text-gray-500">
                        {session.timestamp.toLocaleTimeString()}
                      </span>
                      <div className="flex items-center gap-1">
                        <CheckCircle size={14} className="text-green-600" />
                        <span className="text-xs text-gray-500">
                          {Math.round(session.confidence * 100)}%
                        </span>
                      </div>
                    </div>
                    <p className="text-gray-800 text-sm">{session.transcript}</p>
                  </div>
                ))}
              </div>
            </div>
          )}

          {/*  */}
          <div className="flex gap-3">
            {finalTranscriptRef.current && (
              <button
                onClick={() => processTranscript(finalTranscriptRef.current)}
                disabled={processingTranscript}
                className="btn btn-primary flex-1"
              >
                {processingTranscript ? '...' : ''}
              </button>
            )}
            
            <button
              onClick={clearSessions}
              className="btn btn-secondary px-6"
            >
              
            </button>
            
            <button
              onClick={onClose}
              className="btn btn-secondary px-6"
            >
              
            </button>
          </div>

          {/*  */}
          <div className="mt-6 p-4 bg-blue-50 rounded-lg">
            <h4 className="text-sm font-medium text-blue-900 mb-2"> </h4>
            <ul className="text-sm text-blue-800 space-y-1">
              <li> </li>
              <li> </li>
              <li> </li>
              <li> </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};
